import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import ImageTracking from '@/components/ImageTracking'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'ImageTracking',
      component: ImageTracking
    }
  ]
})



